class StoreUserAcquisition {
    constructor(userAcquistionRepository) {
        this.userAcquistionRepository = userAcquistionRepository;
    }

    execute(userAcquisition) {
        return this.userAcquistionRepository.storeUserAcquisition(userAcquisition);
    }
    
}

export default StoreUserAcquisition;


