class InMemoryUserAcquisitonStore {

    constructor() {
        this.acquisitions = []
    }


    acquistions() {
        return Promise.resolve(this.acquisitions);
    }

    storeUserAcquisition(acquisition) {
        this.acquisitions.push(acquisition);
        return Promise.resolve(null);
    }

}



export default InMemoryUserAcquisitonStore;


